/* seeresults css starts */
.result {
  height: 958px;
  background-color: #f2f2f2;
  padding: 100px 0px;
}
.result .qualified {
  padding: 100px 77px;
  border-radius: 6px;
  background-color: #1c1d21;
  display: inline-block;
  float: right;
  margin-right: 10px;
  position: relative;
  max-width: 508px;
}
@media screen and (max-width: 1024px) and (orientation: landscape) {
