    padding: 100px 66px 25px;
    margin-right: -10px;
  }
}
.result .qualified::before {
  content: "";
  /* background-image: url(/assets/images/group.svg); */
  background-repeat: no-repeat;
  height: 110px;
  width: 150px;
  position: absolute;
  z-index: 1;
  top: 50%;
